<template>
  <div class="Home">
    <div v-html="this.HomeContent" class="MarkdownReader"></div>
  </div>
</template> 

<script>
/**
 * GitHub API URl
 */
const GITHUB_API_REPOS_URL = "https://api.github.com/repos";
const OWNER = "PlayerNguyen";
const REPO = "PlayerNguyenCore";

const BUILT_URL = `${GITHUB_API_REPOS_URL}/${OWNER}/${REPO}`;
/**
 * Axios getters
 */
import axios from "axios";
/**
 * Markdown language supoport
 *
 */
import md from "marked";
/**
 * Base64 decode
 * @deprecated unused
 */
// import Base64 from 'base-64'

export default {
  name: "Home",
  title: "Home",
  components: {},
  data() {
    return {
      ReleaseData: null,
      HomeContent: "",
    };
  },
  mounted() {
    // Get repos  API
    axios.get(BUILT_URL + "/releases").then((e) => {
      console.log(e.data);

      this.ReleaseData = e.data;
    });
    axios.get(BUILT_URL + "/readme").then((e) => {
      axios.get(e.data.download_url).then((e1) => {
        console.log()
        this.HomeContent = md(e1.data);
      });
    });
  },
};
</script>

<style lang="sass" scoped> 
.MarkdownReader 
  color: #24292e
  *
    padding-left: 10em !important
</style>